import { FC, ReactNode } from "react";
import { ReactVisualEditorBlock, ReactVisualEditorComponent } from "../utils/editorUtils";
export enum BlockResizeDirection {
  start = "start",
  center = "center",
  end = "end",
}
export const ReactVisualBlockResize: FC<{
  component: ReactVisualEditorComponent;
  onMouseDown?: (e: React.MouseEvent<HTMLDivElement>, direction: { horizontal: BlockResizeDirection; vertical: BlockResizeDirection }, block: ReactVisualEditorBlock) => void;
}> = (props: any) => {
  const render: ReactNode[] = [];
  if (props.component.resize!.height) {
    render.push(<div className="block-resize block-resize-top" key="top" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.center, vertical: BlockResizeDirection.start }, props.block)} />);
    render.push(<div className="block-resize block-resize-bottom" key="bottom" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.center, vertical: BlockResizeDirection.end }, props.block)} />);
  }

  if (props.component.resize!.width) {
    render.push(<div className="block-resize block-resize-left" key="left" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.start, vertical: BlockResizeDirection.center }, props.block)} />);
    render.push(<div className="block-resize block-resize-right" key="right" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.end, vertical: BlockResizeDirection.center }, props.block)} />);
  }

  if (props.component.resize!.width && props.component.resize!.height) {
    render.push(<div className="block-resize block-resize-top-left" key="top-left" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.start, vertical: BlockResizeDirection.start }, props.block)} />);
    render.push(<div className="block-resize block-resize-top-right" key="top-right" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.end, vertical: BlockResizeDirection.start }, props.block)} />);
    render.push(<div className="block-resize block-resize-bottom-left" key="bottom-left" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.start, vertical: BlockResizeDirection.end }, props.block)} />);
    render.push(<div className="block-resize block-resize-bottom-right" key="bottom-right" onMouseDown={(e) => props.onMouseDown!(e, { horizontal: BlockResizeDirection.end, vertical: BlockResizeDirection.end }, props.block)} />);
  }
  return <>{render}</>;
};
