import { Button, Form, InputNumber, Select } from "antd";
import Input from "antd/es/input/Input";
import deepcopy from "deepcopy";
import { SketchPicker } from "react-color";
import { FC, ReactNode, useEffect, useState } from "react";
import { ReactVisualEditorBlock, ReactVisualEditorConfig, ReactVisualEditorValue } from "../utils/editorUtils";
import { VisualEditorProps, VisualEditorPropsType } from "../utils/ReactVisualEditor.props";
import { TablePropEditor } from "./TablePropEditor";

export const ReactVisualEditorOperator: FC<{
  selectBlock?: ReactVisualEditorBlock;
  value: ReactVisualEditorValue;
  config: ReactVisualEditorConfig;
  updateBlock: (newBlock: ReactVisualEditorBlock, oldBlock: ReactVisualEditorBlock) => void;
  updateValue: (val: ReactVisualEditorValue) => void;
}> = (props) => {
  const [editData, setEditData] = useState({} as any);
  const [form] = Form.useForm();
  useEffect(() => {
    methods.reset();
  }, [props.selectBlock]);

  const methods = {
    apply: () => {
      if (!props.selectBlock) {
        /*当前编辑容器属性*/
        props.updateValue({
          ...props.value,
          container: editData,
        });
      } else {
        /*当前编辑block数据的属性*/
        props.updateBlock(deepcopy(editData), props.selectBlock);
      }
    },
    reset: () => {
      let data: any;
      if (!props.selectBlock) {
        data = deepcopy(props.value.container);
      } else {
        data = deepcopy(props.selectBlock);
      }
      setEditData(data);
      form.resetFields();
      form.setFieldsValue(data);
    },
    onFormValuesChange: (changeValues: any, values: any) => {
      setEditData({
        ...editData,
        ...values,
      });
    },
  };
  let render: ReactNode[] = [];
  if (!props.selectBlock) {
    render.push(
      <Form.Item label="宽度" name="width" key="container-width">
        <InputNumber step={100} min={0} precision={0}></InputNumber>
      </Form.Item>
    );
    render.push(
      <Form.Item label="高度" name="height" key="container-height">
        <InputNumber step={100} min={0} precision={0}></InputNumber>
      </Form.Item>
    );
  } else {
    const component = props.config.componentMap[props.selectBlock.componentKey];
    if (component) {
      render.push(...Object.entries(component.props || {}).map(([propName, propConfig]) => renderEditor(propName, propConfig)));
      render.push(
        ...Object.entries(component.model || {}).map(([modelProp, modelName]) => (
          <Form.Item label={modelName} name={["model", modelProp]} key={`model_${modelProp}`}>
            <Input></Input>
          </Form.Item>
        ))
      );
    }
  }
  return (
    <div className="react-visual-editor-operator">
      <div className="react-visual-editor-operator-title">{!!props.selectBlock ? "编辑元素" : "编辑容器"}</div>
      <Form layout="vertical" form={form} onValuesChange={methods.onFormValuesChange}>
        {render}
        <Form.Item key="operator">
          <Button type="primary" style={{ marginRight: "8px" }} onClick={methods.apply}>
            应用
          </Button>
          {/* <Button onClick={methods.reset}>重置</Button> */}
        </Form.Item>
      </Form>
    </div>
  );
};

function renderEditor(propsName: string, propsConfig: VisualEditorProps) {
  switch (propsConfig.type) {
    case VisualEditorPropsType.text:
      return (
        <Form.Item label={propsConfig.name} name={["props", propsName]} key={`props_${propsName}`}>
          <Input></Input>
        </Form.Item>
      );
    case VisualEditorPropsType.select:
      return (
        <Form.Item label={propsConfig.name} name={["props", propsName]} key={`props_${propsName}`}>
          <Select>
            {propsConfig.options!.map((opt, index) => (
              <Select.Option value={opt.value} key={index}>
                {opt.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      );
    case VisualEditorPropsType.color:
      return (
        <Form.Item label={propsConfig.name} name={["props", propsName]} key={`props_${propsName}`}>
          <SketchPicker />
        </Form.Item>
      );
    case VisualEditorPropsType.table:
      return (
        <Form.Item label={propsConfig.name} name={["props", propsName]} key={`props_${propsName}`}>
          <TablePropEditor config={propsConfig} />
        </Form.Item>
      );
  }
}
