import classNames from "classnames";
import { FC, MouseEvent, ReactNode, useEffect, useMemo, useRef } from "react";
import { useUpdate } from "../hooks/useUpdate";
import { ReactVisualEditorBlock, ReactVisualEditorConfig } from "../utils/editorUtils";

export const ReactVisualBlock: FC<{
  children?: ReactNode;
  block: ReactVisualEditorBlock;
  config: ReactVisualEditorConfig;
  onMouseDown?: (ev: MouseEvent<HTMLDivElement>) => void;
  onContextMenu?: (ev: MouseEvent<HTMLDivElement>) => void;
  formData: Record<string, any>;
  onFormDataChange: (FormData: Record<string, any>) => void;
}> = (props) => {
  const elRef = useRef({} as HTMLDivElement);
  const { forceUpdate } = useUpdate();
  const styles = useMemo(() => {
    return {
      top: `${props.block.top}px`,
      left: `${props.block.left}px`,
      opacity: props.block.adjustPosition ? "0" : "",
      zIndex: props.block.zIndex,
    };
  }, [props.block.top, props.block.left, props.block.adjustPosition, props.block.zIndex]);

  const classes = useMemo(
    () =>
      classNames("react-visual-editor-block", {
        "react-visual-editor-block-focus": props.block.focus,
      }),
    [props.block.focus]
  );
  const component = props.config.componentMap[props.block.componentKey];
  let render: any;
  if (!!component) {
    render = component.render({
      size: props.block.hasResize
        ? (() => {
            let styles = {
              height: undefined as undefined | string,
              width: undefined as undefined | string,
            };
            component.resize && !!component.resize.width && (styles.width = `${props.block.width}px`);
            component.resize && !!component.resize.height && (styles.height = `${props.block.height}px`);
            return styles;
          })()
        : {},
      props: props.block.props || {},
      model: Object.entries(component.model || {}).reduce((prev, item) => {
        const [modelProp] = item;
        prev[modelProp] = {
          value: !props.block.model || !props.block.model[modelProp] ? null : props.formData[props.block.model[modelProp]],
          onChange: (e: any) => {
            if (!props.block.model || !props.block.model[modelProp]) {
              return;
            }
            let val: any;
            if (!e || !("target" in e)) {
              val = e;
            } else {
              val = e.target.value;
            }
            props.onFormDataChange({ ...props.formData, [props.block.model[modelProp]]: val });
          },
        };
        return prev;
      }, {} as Record<string, { value: any; onChange: (val: any) => void }>),
    });
  }
  useEffect(() => {
    if (props.block.adjustPosition) {
      const { top, left } = props.block;
      const { height, width } = elRef.current.getBoundingClientRect();
      props.block.adjustPosition = false;
      props.block.top = top - height / 2;
      props.block.left = left - width / 2;
      props.block.width = elRef.current.offsetWidth;
      props.block.height = elRef.current.offsetHeight;
      forceUpdate();
    }
  }, [forceUpdate, props.block]);

  return (
    <div className={classes} style={styles} ref={elRef} onMouseDown={props.onMouseDown} onContextMenu={props.onContextMenu}>
      {render}
      {props.children}
    </div>
  );
};
