import { VisualEditorProps } from "./ReactVisualEditor.props";

// 容器内每个元素数据类型
export interface ReactVisualEditorBlock {
  componentKey: string; //component对象key，通过这个key找到config中的componen
  top: number; //定位
  left: number;
  width: number; //元素宽高
  height: number;
  adjustPosition: boolean; //是否需要调整位置
  focus: boolean; //是否被选中
  zIndex: number;
  hasResize: boolean; //是否调整过大小
  props?: Record<string, any>;
  model?: Record<string, string>; //绑定值
}
//编辑器的数据类型
export interface ReactVisualEditorValue {
  container: {
    height: number; //容器宽高
    width: number;
  };
  blocks: ReactVisualEditorBlock[];
}
// 编辑器预定义组件类型
export interface ReactVisualEditorComponent {
  key: string; //组件唯一标志
  name: string; //组件预览名称
  preview: () => JSX.Element; //组件预览内容
  render: (data: { size: { width?: string; height?: string }; props: Record<string, any>; model: Record<string, any> }) => JSX.Element; //组件渲染内容
  resize?: {
    height?: boolean; //是否可拖拽高度
    width?: boolean;
  };
  props?: { [k: string]: VisualEditorProps };
  model?: { [k: string]: string };
}
export function createVisualBlock({ top, left, component }: { top: number; left: number; component: ReactVisualEditorComponent }): ReactVisualEditorBlock {
  return {
    top,
    left,
    componentKey: component.key,
    adjustPosition: true,
    focus: false,
    zIndex: 0,
    width: 0,
    height: 0,
    hasResize: false,
  };
}
// 创建编辑器预设配置信息对象
export function createVisualConfig() {
  //用于block数据，通过componentKey找到这个组件对象，使用component对象的render渲染内容
  const componentMap: { [k: string]: ReactVisualEditorComponent } = {};
  //用户在menu中渲染预定义组件列表
  const componentArray: ReactVisualEditorComponent[] = [];

  // 注册一个组件
  function registerComponent<_, Props extends { [k: string]: VisualEditorProps } = {}, Model extends { [k: string]: string } = {}>(
    key: string,
    options: {
      name: string; //组件预览名称
      preview: () => JSX.Element; //组件预览内容
      render: (data: {
        size: { width?: string; height?: string };
        props: { [k in keyof Props]?: any };
        model: {
          [k in keyof Model]: { value: any; onChange: (val: any) => void };
        };
      }) => JSX.Element; //组件渲染内容
      resize?: {
        height?: boolean; //是否可拖拽高度
        width?: boolean;
      };
      props?: Props;
      model?: Model;
    }
  ) {
    //组件已存在，则删除
    if (componentMap[key]) {
      const index = componentArray.indexOf(componentMap[key]);
      componentArray.splice(index, 1);
    }
    const newComponent = {
      key,
      ...options,
    };
    componentArray.push(newComponent as any);
    componentMap[key] = newComponent as any;
  }

  return {
    componentMap,
    componentArray,
    registerComponent,
  };
}

export type ReactVisualEditorConfig = ReturnType<typeof createVisualConfig>;
