import { useCallback, useEffect, useRef, useState } from "react";
import { KeyboardCode } from "./keyboard-code";

export interface CommandExecute {
  undo?: () => void;
  redo: () => void;
}

interface Command {
  name: string; //命令的唯一标志
  keyboard?: string | string[]; //命令监听的快捷键
  execute: (...args: any[]) => CommandExecute; //命令执行后需要返回的操作
  followQueue?: boolean; //命令执行完之后是否要将命令执行到undo，redo存入命令队列
  init?: () => (() => void) | undefined; //初始化命令函数
}

export function useCommander() {
  const [state] = useState(() => ({
    current: -1, //当前命令队列中，最后执行的命令返回的CommanExecute对象
    queue: [] as CommandExecute[], //命令队列
    commandArray: [] as { current: Command }[], //预定义命令数组
    commands: {} as Record<string, (...args: any) => void>, //通过command name 执行 command动作的一个包装
    destroyList: [] as ((() => void) | undefined)[], //所有命令在组建销毁之前，需要执行的消除副作用的所有函数数组
  }));

  //注册删除命令
  const useRegistry = useCallback((command: Command) => {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const commandRef = useRef(command);
    commandRef.current = command;
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useState(() => {
      if (state.commands[command.name]) {
        const existIndex = state.commandArray.findIndex((item) => item.current.name === command.name);
        state.commandArray.splice(existIndex, 1);
      }
      state.commandArray.push(commandRef);
      state.commands[command.name] = (...args: any[]) => {
        const { redo, undo } = commandRef.current.execute(...args);
        redo();
        //如果命令执行后，不需要进入命令队列，保留current及其之前的命令
        if (commandRef.current.followQueue === false) {
          return;
        }
        //否则将命令队列中剩余的命令去掉，保留current及其之前的命令
        let { queue, current } = state;
        if (queue.length > 0) {
          queue = queue.splice(0, current + 1);
          state.queue = queue;
        }
        //设置命令队列中最后一个命令为当前执行的命令
        queue.push({ undo, redo });
        // 索引加一，指向队列最后一个命令
        state.current = current + 1;
      };
    });
  }, []);

  const [keyboardEvent] = useState(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (document.activeElement !== document.body) {
        return;
      }

      const { keyCode, shiftKey, altKey, ctrlKey, metaKey } = e;
      let keyString: string[] = [];
      if (ctrlKey || metaKey) keyString.push("ctrl");
      if (shiftKey) keyString.push("shift");
      if (altKey) keyString.push("alt");
      keyString.push(KeyboardCode[keyCode]);
      const keyNames = keyString.join("+");
      state.commandArray.forEach(({ current: { keyboard, name } }) => {
        if (!keyboard) {
          return;
        }
        const keys = Array.isArray(keyboard) ? keyboard : [keyboard];
        if (keys.indexOf(keyNames) > -1) {
          state.commands[name]();
          e.stopPropagation();
          e.preventDefault();
        }
      });
    };
    const init = () => {
      window.addEventListener("keydown", onKeyDown, true);
      return () => {
        window.removeEventListener("keydown", onKeyDown, true);
      };
    };
    return { init };
  });
  // useCommander初始化函数，负责初始化键盘监听事件，调用命令的初始化逻辑
  const useInit = useCallback(() => {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useState(() => {
      state.commandArray.forEach((command) => !!command.current.init && state.destroyList.push(command.current.init()));
      state.destroyList.push(keyboardEvent.init());
    });
    //注册撤回命令（撤回命令执行结果不需要进入命令队列）
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useRegistry({
      name: "undo",
      keyboard: "ctrl+z",
      followQueue: false,
      execute: () => {
        return {
          redo: () => {
            if (state.current === -1) {
              return;
            }
            const queueItem = state.queue[state.current];
            if (!!queueItem) {
              !!queueItem.undo && queueItem.undo();
              state.current--;
            }
          },
        };
      },
    });
    // 册重做命令（重做命令执行结果不需要进入命令队列）
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useRegistry({
      name: "redo",
      keyboard: ["ctrl+y", "ctrl+shift+z"],
      followQueue: false,
      execute: () => {
        return {
          redo: () => {
            const queueItem = state.queue[state.current + 1];
            if (!!queueItem) {
              queueItem.redo();
              state.current++;
            }
          },
        };
      },
    });
  }, []);

  useEffect(() => {
    return () => {
      state.destroyList.forEach((fn) => !!fn && fn());
    };
  }, []);
  return {
    state,
    useRegistry,
    useInit,
  };
}
