import { createVisualConfig } from "./utils/editorUtils";
import { Button, Input, Select } from "antd";
import { createColorProp, createSelectProp, createTableProp, createTextProp } from "./utils/ReactVisualEditor.props";

export const visualConfig = createVisualConfig();

visualConfig.registerComponent("text", {
  name: "文本",
  preview: () => <span>预览文本</span>,
  render: ({ props }) => <span style={{ color: !props.color ? "" : props.color.hex, fontSize: props.size }}>渲染文本</span>,
  resize: {
    width: false,
    height: false,
  },
  props: {
    text: createTextProp("显示文本"),
    color: createColorProp("字体颜色"),
    size: createSelectProp("字体大小", [
      { label: "14px", value: "14px" },
      { label: "18px", value: "18px" },
      { label: "24px", value: "24px" },
    ]),
  },
});

visualConfig.registerComponent("button", {
  name: "按钮",
  preview: () => <Button type="primary">预览按钮</Button>,
  render: ({ size, props }) => (
    <Button type={props.type || "primary"} style={size} size={props.size}>
      {props.text || "渲染的按钮"}
    </Button>
  ),
  resize: {
    width: true,
    height: true,
  },
  props: {
    text: createTextProp("按钮文本"),
    type: createSelectProp("按钮类型", [
      { label: "默认", value: "default" },
      { label: "基础", value: "primary" },
      { label: "线框", value: "ghost" },
      { label: "虚线", value: "dashed" },
      { label: "链接", value: "link" },
      { label: "文本", value: "text" },
    ]),
    size: createSelectProp("大小尺寸", [
      { label: "大", value: "large" },
      { label: "中", value: "middle" },
      { label: "小", value: "small" },
    ]),
  },
});

visualConfig.registerComponent("input", {
  name: "输入框",
  preview: () => <Input />,
  render: ({ size, model }) => <Input style={size} value={model?.default.value} onChange={model?.default.onChange} />,
  resize: {
    width: true,
  },
  model: {
    default: "绑定字段",
  },
});

visualConfig.registerComponent("select", {
  name: "下拉框",
  preview: () => (
    <Select>
      <Select.Option value="">下拉框 </Select.Option>
    </Select>
  ),
  render: ({ props, size }) => (
    <Select style={{ width: size.width || "225px" }} key={(props.options || []).map(({ label, val }: { label: string; val: string }) => `${label}_${val}`).join(".")}>
      {(props.options || []).map((opt: any, index: number) => (
        <Select.Option value={opt.val} key={index}>
          {opt.label}
        </Select.Option>
      ))}
    </Select>
  ),
  props: {
    options: createTableProp("下拉选项", "label", [
      { name: "选项显示值", field: "label" },
      { name: "选项值", field: "val" },
      { name: "备注", field: "comments" },
    ]),
  },
  resize: {
    width: true,
  },
});
