export enum VisualEditorPropsType {
  text = "text",
  input = "input",
  color = "color",
  select = "select",
  table = "table",
}
export type VisualEditorProps = ReactVIsualEditorTextProps | ReactVIsualEditorSelectProps | ReactVIsualEditorColorProps | ReactVIsualEditorTableProps;

export interface ReactVIsualEditorTextProps {
  name: string;
  type: VisualEditorPropsType.text;
}
export function createTextProp(name: string): ReactVIsualEditorTextProps {
  return { name, type: VisualEditorPropsType.text };
}

export type ReactVIsualEditorSelectProps = {
  name: string;
  type: VisualEditorPropsType.select;
  options: { label: string; value: string }[];
};
export function createSelectProp(name: string, options: { label: string; value: string }[]): ReactVIsualEditorSelectProps {
  return { name, options, type: VisualEditorPropsType.select };
}

export interface ReactVIsualEditorColorProps {
  name: string;
  type: VisualEditorPropsType.color;
}
export function createColorProp(name: string): ReactVIsualEditorColorProps {
  return { name, type: VisualEditorPropsType.color };
}

export interface ReactVIsualEditorTableProps {
  name: string;
  type: VisualEditorPropsType.table;
  showField: string;
  columns: {
    name: string;
    field: string;
  }[];
}
export function createTableProp(name: string, showField: string, columns: { name: string; field: string }[]): ReactVIsualEditorTableProps {
  return { name, type: VisualEditorPropsType.table, columns, showField };
}
