import { useState } from "react";
// import { useCallbackRef } from "./hooks/useCallbackRef";
import { ReactVisualEditor } from "./packages/ReactVisualEditor";
import { ReactVisualEditorValue } from "./utils/editorUtils";
import { visualConfig } from "./visual.config";
function App() {
  /*
   addEventListener只在移动开始绑定一次，mousemove过程中获取不到最新的值
   通过引用的方式获取最新值
   每个变量都要包一层，比较麻烦
   自定义useCallbackRef，封装方法
  */
  // const posRef = useRef(pos);
  // posRef.current = pos;
  const [formData, onFormDataChange] = useState({
    username: "admin01",
    minLevel: 100,
    maxLevel: 200,
  } as any);
  const [editorValue, setEditorValue] = useState(() => {
    const val: ReactVisualEditorValue = {
      container: {
        width: 700,
        height: 1000,
      },
      blocks: [
        {
          componentKey: "text",
          top: 100,
          left: 100,
          adjustPosition: false,
          focus: false,
          zIndex: 0,
          width: 28,
          height: 22,
          hasResize: false,
        },
        {
          componentKey: "button",
          top: 200,
          left: 200,
          adjustPosition: false,
          focus: false,
          zIndex: 0,
          width: 63,
          height: 32,
          hasResize: false,
        },
        {
          componentKey: "input",
          top: 300,
          left: 300,
          adjustPosition: false,
          focus: false,
          zIndex: 0,
          width: 182,
          height: 31,
          hasResize: false,
        },
      ],
    };

    return val;
  });
  return (
    <div>
      <ReactVisualEditor config={visualConfig} value={editorValue} onChange={setEditorValue} formData={formData} onFormDataChange={onFormDataChange}></ReactVisualEditor>
    </div>
  );
}
export default App;
